import opg from 'ts/opg.ts';

import {Cache} from 'ts/util/store';
import {Languages} from '../../../ts/util/Languages';
import Panel from '../../../ts/ui/Panel';
import {Combo} from '../../../ts/ui/Combo';
import {store} from '../../../ts/util/store';
import Table from '../../../ts/ui/Table';
import {url} from '../../../ts/util/utils';
const cache = Cache.getInstance();


console.log('Languages:', Languages);

const lpg = Languages.package;

const moduleName = lpg.moduleName;

opg.api({
	'queryActivities!post' : 'activity/QueryActivitys',
	setStick: 'activity/SetActivityStick',
	cancelStick: 'activity/CancelActivityStick',
	'setStatus!post': 'activity/EnableOrDisableActivityById',
});



let panel: Panel = opg.wrapPanel('#tbSearch', {
	title: `${moduleName}${lpg.search}`,
	btnSearchText: `<i class="ico-find"></i> ${lpg.search}`,
	btnSearchClick: function () {
		let param = $('#tbSearch').fieldsToJson();

		if (param.beginDate && param.beginDate.indexOf(' ') < 0) {
			param.beginDate += ' 00:00:00';
		}
		if (param.endDate && param.endDate.indexOf(' ') < 0) {
			param.endDate += ' 23:59:59';
		}
		//console.log(panel.jq, param);
		tb.update(param);
	},
});

jQuery.datetimepicker.setLocale(Languages.current);
Combo.makeClearableInput($('#beginDate').datetimepicker({
	timepicker: false,
	closeOnDateSelect: true,
	format: 'Y-m-d',
}), $({}));
Combo.makeClearableInput($('#endDate').datetimepicker({
	timepicker: false,
	closeOnDateSelect: true,
	format: 'Y-m-d',
}), $({}));


const permission = store.get('permission');
let tbButtons = [];
if (permission) {
	if (permission['AddActivity']) {
		tbButtons.push({id: 'btnAdd', className: 'btn-create', html: `<i class="ico ico-create"></i> ${lpg.add}`});
	}
}

let tb: Table = opg('#tb').table({
	api: opg.api.queryActivities ,
	param: {informationType: 0},
	titleBar: {
		title: `${moduleName}${lpg.list}`,
		buttons: tbButtons,
	},
	columns: [
		{
			text: 'ID', width: 120,
			src: 'id',
		},
		{
			text: lpg.topic,
			src: 'title',
		},
		{
			text: lpg.publisher,
			src: 'from',
			width: 150,
		},
		{
			text: lpg.stick,
			src: 'stick',
			width: 60,
			render: v => v ? lpg.yes : lpg.no,
		},
		{
			text: lpg.status,
			src: 'status',
			width: 100,
			render: v => v ? lpg.online : lpg.offline,
		},
		{
			text: lpg.process,
			src: 'id',
			width: 180,
			align: 'left',
			render: function (id, index, row) {
				let html = `<button data-id="${id}" data-index="${row[':index']}" class="btn-mini btn-info btnEdit">${lpg.edit}</button> `;

				if (!row.stick) {
					html += `<button data-id="${id}" class="btn-mini btn-success btnSetStick">${lpg.stick}</button> `;
				}
				else {
					html += `<button data-id="${id}" class="btn-mini btn-warning btnCancelStick">${lpg.cancelStick}</button> `;
				}

				if (row.status) {
					html += `<button data-id="${id}" class="btn-mini btn-danger btnOffline">${lpg.offline}</button>`;
				}
				else {
					html += `<button data-id="${id}" class="btn-mini btn-success btnOnline">${lpg.online}</button>`;
				}
				return html;
			},
		},
	],
	pagination: true,
});

const infoPage = __uri('/pages/content/activity/edit.html');
//Add
$('#btnAdd').click(function () {

	let pop = opg.popTop(`<iframe src="${infoPage}" />`, {
		title: `${lpg.add}${moduleName}`,
		btnMax: true,
		width: 800,
		height: 550,
		onClose: function () {
			tb.update();
		},
	});
});

//edit
tb.tbody.on('click', '.btnEdit', function () {
	let btn = $(this), id = btn.data('id'), index = btn.data('index');

	let row = tb.data[index];
	row.enableStick = row.stick ? 1 : 0;

	cache.set('row', row);

	let src = url.setParam(infoPage, {id});

	let pop = opg.popTop(`<iframe src="${src}" />`, {
		title: `${lpg.edit}: ${row.title}`,
		btnMax: true,
		width: 800,
		height: 550,
		onClose: function () {
			cache.remove('row');
			tb.update();
		},
	});
});

//offline
tb.tbody.on('click', '.btnOffline', function () {
	let btn = $(this), id = btn.data('id');
	opg.api.setStatus({id, status: 0}, () => tb.update());
});
//online
tb.tbody.on('click', '.btnOnline', function () {
	let btn = $(this), id = btn.data('id');
	opg.api.setStatus({id, status: 1}, () => tb.update());
});


//stick
tb.tbody.on('click', '.btnSetStick', function () {
	let btn = $(this), activityId = btn.data('id');
	opg.api.setStick({activityId}, () => tb.update());
});
//stick
tb.tbody.on('click', '.btnCancelStick', function () {
	let btn = $(this), activityId = btn.data('id');
	opg.api.cancelStick({activityId}, () => tb.update());
});
